@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Roboto:wght@300&family=Sofia+Sans+Condensed:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Montserrat:wght@500&family=Roboto:wght@300&family=Sofia+Sans+Condensed:wght@700&family=Source+Serif+4:opsz,wght@8..60,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ibarra+Real+Nova&family=Montserrat:wght@500&family=Sofia+Sans+Condensed:wght@700&family=Source+Serif+4:opsz,wght@8..60,500&family=Spectral:ital,wght@0,200;1,200&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
  overflow-x: hidden;
}

body {
  font-family: Helvetica, sans-serif;
  text-rendering: optimizeLegibility;
}

header {
  display: flex;
  padding: 20px 40px;
  background-color: #fff;
  align-items: center;
  justify-content: space-between;
}
header .logo {
  width: 3rem;
  margin-right: 1.8em;
}
header nav {
  align-items: center;
  color: #000;
  font-size: 0.7em;
  display: flex;
  flex: auto;
  text-decoration: none;
  justify-content: space-between;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2.2px;
  text-transform: uppercase;
  gap: 6.2rem;
}
header ul {
  display: flex;
}
header .nav-title {
  display: inline-block;
  text-decoration: none;
  color: #000;
  padding: 7.5px 15px;
  vertical-align: middle;
}
header .button {
  padding: 16px 32px;
  border-radius: 0;
  border: 1px solid #414141;
  margin-right: 1.4em;
}
header .header-icons {
  display: flex;
  align-items: center;
}
header .header-icons .list-icon {
  width: 28px;
  height: 28px;
  padding: 12px;
}

.hero {
  height: 100vh;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("https://paint-box.com/cdn/shop/files/Web_Banner2.webp?v=1697146136&width=2400");
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.hero h4 {
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  letter-spacing: 2.2px;
  margin-bottom: 15px;
  text-transform: uppercase;
}
.hero h3 {
  font-size: 64px;
  font-family: "Spectral", serif;
  text-align: center;
  margin-bottom: 24px;
}
.hero p {
  font-family: "Spectral", serif;
  font-style: italic;
  font-size: 1.6em;
  margin-bottom: 5px;
  margin-top: 5px;
  vertical-align: middle;
}
.hero .cta-shop {
  background: #fff;
  border: none;
  color: #000;
  margin: 4px 3px 2px 0;
  padding: 18px 20px;
  width: 240px;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2.2px;
  vertical-align: center;
  text-align: center;
  margin-top: 24px;
}